.header {
    width: 90vw;
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 0 24px;
    border-bottom: 2px solid greenyellow;
}

.header__menu {
    display: -ms-flexbox;
    display: flex;
    color: red;
    list-style: none;
    
}

.header__menu_item{
    -ms-flex-item-align: end;
        align-self: flex-end;
    text-transform: uppercase;
    padding: 0 8px;
}


