.form {
    min-width: 240px;
    height: 320px;
    margin: 24px 16px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -webkit-box-shadow: 0 0 2px 2px rgba(33, 33, 33, .5);
            box-shadow: 0 0 2px 2px rgba(33, 33, 33, .5);
}

.form__label {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    margin: 8px 8px;
}

.form__submit {
    outline: 0;
    border: 0;
    padding: 10px 20px;
    margin: 0 8px;
    border-radius: 2px;
    text-transform: uppercase;
    color: #fff;
    font-size: 16px;
    background-color: #3f51b5;
    cursor: pointer;
    -webkit-box-shadow: 0 3px 3px 0 rgba(0, 0, 0, .3);
            box-shadow: 0 3px 3px 0 rgba(0, 0, 0, .3);
    -webkit-transition: all .2s linear;
    -o-transition: all .2s linear;
    transition: all .2s linear;
}





